const DrumPad = (props) => {
  return (
    <div>
      <button
        className="drum-pad"
        id={props.name}
        onClick={props.playSound.bind(this, props.letter)}
      >

      {props.letter}

      <audio 
        id={props.letter} 
        className="clip" 
        preload="auto" 
        src={props.url} 
        type="audio/mpeg" />
      </button>
    </div>
  );
}

class DrumMachine extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      currKey: "Press or Click one of  the Keys below â†“"
    }
  }

  componentDidMount() {
    document.addEventListener("keydown", this.playSound);
  }
  componentWillUnmount() {
    document.removeEventListener("keydown", this.playSound);
  }

  playSound = (event) => {
    const letter = event.length === 1 ? event.toUpperCase() : event.key.toUpperCase();
    const sounds = this.props.sounds;
    const found = sounds.find((sound)=> {
      return sound["letter"] === letter
    })
    !found 
    ? alert("Please enter a valid key")
    : (() => {
      this.setState({currKey: found["name"]})
      document.getElementById(letter).play().catch(() => console.log("Error Playing Music"))
    })()
  }

  render() {
    const sounds = this.props.sounds
    const drumPads = sounds.map((sound) => {
      return (
        <DrumPad
          url={sound["url"]}
          key={sound["letter"]}
          name={sound["name"]}
          playSound={this.playSound}
          letter={sound["letter"]}
        />
      );
    })

    // styles START
    const displayParentStyle = { 
      display: "flex",
      flex: "1 0 100%",
      margin: "1.5rem auto",
      marginBottom: "2.5rem" 
    }
    // styles FINISH

    return (
      <div id = "drum-machine">
        <div style={displayParentStyle}>
          <div id = "display"> {this.state.currKey} </div>
        </div>
        <div className="drumPads">
          {drumPads}
        </div>
      </div>
    );
  }

}

ReactDOM.render(<DrumMachine sounds={SOUNDS} />, document.getElementById("root"));